// 
import request from "@/utils/request"
// 
import { getData } from '@/store'

// 
export const getImgs = (params) => {
    return request({
        method: 'get',
        url: `/mp/v1_0/user/images`,
        // 
        params:params
    })
}

// 
export const switchCollect = (id,collect) => {
    return request({
        method: 'put',
        url: `/mp/v1_0/user/images/${id}`,
       data:{
        collect
       }
    })
}

// 
export const delImg = (id) => {
    return request({
        method: 'delete',
        url: `/mp/v1_0/user/images/${id}`
    })
}
