<template>
  <div id="cover">
    <!-- number -->
    <div v-for="(item,index) in picknum" :key="item" @click="opendialog(index)" class="operateBox">
      <span></span>
      <!--  
      img-->
      <img :src="imageArr[index]" class="img" v-show="imageArr[index]"/>
    </div>
    <!--  -->
    <el-dialog :append-to-body="true" :visible.sync="dialogVisible" width="720px">
      <el-card class="box-card">
        <div style="padding-bottom: 20px;">
          <el-radio-group v-model="collect" size="mini" @change="changeVal">
            <el-radio-button :label="false"></el-radio-button>
            <el-radio-button :label="true"></el-radio-button>
          </el-radio-group>
        </div>
        <el-row :gutter="10">
          <el-col
            v-for="(image) in imageList"
            @click.native="select(image)"
            :key="image.id"
            class="img_item"
            :xs="12"
            :sm="6"
            :md="6"
            :lg="4"
          >
            <div class="img_box">
              <img :src="image.url" alt />
              <div class="selected" v-show="image.isShow">
                <img src="./img/selected.png" alt />
              </div>
            </div>
          </el-col>
        </el-row>
        <el-pagination
          style="margin-top:10px;"
          background
          layout="sizes, prev, pager, next"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="10"
          @current-change="currentPageChange"
          @size-change="pageSizeChange"
          :total="total_cont"
        ></el-pagination>
      </el-card>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="confirm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getImgs, switchCollect } from "@/api/images";

export default {
  props: {
    picknum: {
      type: Number, //
    },
    images: {
      type: Array,//
      default:function(){ //images
        return []
      }
    },
  },
  data() {
    return {
      imageList: [], // 
      total_cont: 0, // 
      curPage: 1, // 
      per_page: 10, // 
      collect: false, //  
      dialogVisible: false,
      curImg: null, //
      curIndex: 0, //
      imageArr: [], //
    };
  },
  methods: {
    //3dialogdialog
    // 3.1 dialog
    opendialog(index) {
      this.dialogVisible = true;
      //
      this.curIndex = index;
      console.log(this.images);
    },
    // 3.2 dialog
    // 3.2.1 
    getList() {
      getImgs({
        page: this.curPage,
        collect: this.collect,
        per_page: this.per_page,
      }).then((res) => {
        let list = res.data.data.results;
        // isShow
        let newList = list.map((item) => {
          return {
            ...item,
            isShow: false,
          };
        });
        //
        this.imageList = newList;
        this.total_cont = res.data.data.total_count;
      });
    },
    // 3.2.2 
    changeVal(val) {
      //
      this.getList();
    },
    // 3.2.3 
    currentPageChange(val) {
      //
      this.curPage = val;
      this.getList();
    },
    // 3.2.4 
    pageSizeChange(val) {
      //
      this.pageSize = val;
      //
      this.getList();
    },
    // 3.3 
    // v-ifimage.isShow
    //      truefalse
    select(image) {
      //isShowfalse
      this.imageList.forEach((item) => {
        item.isShow = false;
      });
      image.isShow = true;
      //3.4 
      this.curImg = image;
    },

    // 4
    // 4.1 +index
    //     
    //     
    confirm() {
      // 
      this.imageArr[this.curIndex] = this.curImg.url;
      // 
      this.dialogVisible = false;
    },

    // 4.2 image.isShowfalse
    cancel() {
      this.imageList.forEach((item) => {
        item.isShow = false;
      });
      // 
      this.dialogVisible = false;
    },

    // 5images,
    // 
    // 5.1 
    setImages() {
      return this.imageArr
    },
  },
  mounted() {
    this.getList();
    // 0.4 undefined
    // 
    if (this.images.length>0) {
      this.imageArr = this.images;
    }
  },
};
</script>

<style lang="less" scoped>
.operateBox {
  display: inline-block;
  width: 100px;
  height: 100px;
  margin-left: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 13px;
  color: #666;
  text-align: center;
  line-height: 100px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  .img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.img_item {
  position: relative;
  box-sizing: border-box;
  .img_box {
    height: 120px;
    margin-bottom: 10px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
    .selected {
      position: absolute;
      width: 20%;
      left: 0;
      top: 0;
    }
  }
}
.option {
  position: absolute;
  left: 5px;
  right: 5px;
  bottom: 5px;
  height: 30px;
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
  display: flex;
  span {
    flex: 1;
    text-align: center;
    cursor: pointer;
    line-height: 30px;
  }
}
.ql-editor{
  height:400px
}
</style>